<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header fiexdHeader">
      <a @click="()=>{$router.go(-1)}">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left" color="white"></mu-icon>
        </mu-button>
      </a>
      <span>路线信息</span>
    </mu-appbar>
    <div style="margin-top:60px;" class="back">
      <div class="naviTitle">
        <span>线路信息</span>
      </div>
      <!-- <div class="conteBorder"> -->
      <div style class="contentAdd">
        <div v-for="(item,index) in listContentAdd" :key="index" class="conteBorder">
          <div class="flexdis">
            <div style="width:100px;">监测时间</div>
            <div class="dateTime">
              <input type="text" id="input" disabled placeholder :value="item.name" />
            </div>
          </div>
          <div class="flexdis">
            <div style="width:100px;">车辆编号</div>
            <div class="dateTime">
              <input type="text" id="input" disabled placeholder :value="item.vehicleId" />
            </div>
          </div>
          <div class="flexdis">
            <div style="width:100px;">监测人员</div>
            <div class="dateTime">
              <input type="text" id="input" disabled placeholder :value="item.person" />
            </div>
          </div>
          <div class="flexdis">
            <div style="width:100px;">监测类型</div>
            <div class="dateTime">
              <input type="text" id="input" disabled placeholder :value="item.type" />
            </div>
          </div>
          <div class="flexdis">
            <div style="width:100px;">任务编号</div>
            <div class="dateTime">
              <input type="text" id="input" disabled placeholder :value="item.taskId" />
            </div>
          </div>
          <div class="flexdis">
            <div style="width:100px;">点位数量</div>
            <div class="dateTime">
              <input type="text" id="input" disabled placeholder :value="item.pointNumber" />
            </div>
          </div>
        </div>
      </div>
      <!-- </div> -->
      <div class="naviTitle">
        <span>点位数据</span>
      </div>
      <div v-for="(item,index) in list" :key="index" class="pointNews">
        <div style="margin-left:13px;font-weight:600;">
          点位
          <span>{{index+1}}</span>
        </div>
        <mu-expansion-panel
          :expand="panel === 'panel1'+index"
          @change="toggle('panel1'+index)"
          class="padding20"
        >
          <div slot="header">基本信息</div>
          <div>
            <div class="flexdis">
              <div style="width:100px;">监测时间</div>
              <div class="dateTime">
                <input type="text" id="input" disabled placeholder :value="item.gmtCreate" />
              </div>
            </div>
            <div class="flexdis">
              <div style="width:100px;">点位名称</div>
              <div class="dateTime">
                <input type="text" id="input" disabled placeholder :value="item.pollutantName" />
              </div>
            </div>
            <div class="flexdis">
              <div style="width:100px;">经纬度</div>
              <div class="dateTime">
                <input
                  type="text"
                  id="input"
                  disabled
                  placeholder
                  :value="item.longitude+','+item.latitude"
                />
              </div>
            </div>
          </div>
        </mu-expansion-panel>
        <mu-expansion-panel
          :expand="panel === 'panel2'+index"
          @change="toggle('panel2'+index)"
          class="padding20"
        >
          <div slot="header">监测数据</div>
          <div>
            <div class="flexdis1">
              <div style="width:100px;">pH</div>
              <div>
                <span>{{item.ph}}</span>
              </div>
            </div>
            <div class="flexdis1">
              <div style="width:100px;">COD</div>
              <div>
                <span>{{item.cod}}</span>
                <span>mg/L</span>
              </div>
            </div>
            <div class="flexdis1">
              <div style="width:100px;">总铜</div>
              <div>
                <span>{{item.cuprum}}</span>
                <span>mg/L</span>
              </div>
            </div>
            <div class="flexdis1">
              <div style="width:100px;">总氮</div>
              <div>
                <span>{{item.nitrogen}}</span>
                <span>mg/L</span>
              </div>
            </div>
            <div class="flexdis1">
              <div style="width:100px;">氨氮</div>
              <div>
                <span>{{item.ammoniaNitrogen}}</span>
                <span>mg/L</span>
              </div>
            </div>
            <div class="flexdis1">
              <div style="width:100px;">总铬</div>
              <div>
                <span>{{item.chromium}}</span>
                <span>mg/L</span>
              </div>
            </div>
            <div class="flexdis1">
              <div style="width:100px;">总镍</div>
              <div>
                <span>{{item.nickel}}</span>
                <span>mg/L</span>
              </div>
            </div>
            <div class="flexdis1">
              <div style="width:100px;">总磷</div>
              <div>
                <span>{{item.phosphorus}}</span>
                <span>mg/L</span>
              </div>
            </div>
            <div class="flexdis1">
              <div style="width:100px;">总氰化物</div>
              <div>
                <span>{{item.prussiate}}</span>
                <span>mg/L</span>
              </div>
            </div>
          </div>
        </mu-expansion-panel>
        <mu-expansion-panel
          :expand="panel === 'panel3'+index"
          @change="toggle('panel3'+index)"
          class="padding20"
        >
          <div slot="header">现场图片</div>
          <div class="imgscene">
            <img :src="url+item.photo" alt srcset />
          </div>
        </mu-expansion-panel>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  name: "addDataList",
  data() {
    return {
      listContent: [],
      list: [],
      value6: null,
      panel: "",
      listContentAdd: [],
      url: "",
      input: {
        time: "",
        carNum: "",
        peroson: "",
        type: "",
        taskNum: "",
        pointNum: ""
      },
      hello: "1212",
      url: "http://47.112.16.168:",
      loading: false,
      token: ""
    };
  },
  methods: {
    fullscreen() {
      this.loading = this.$loading();
    },
    btnAddNews() {
      this.$router.push({
        path: "/addData",
        query: {
          routingName: this.$route.query.id
        }
      });
    },
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    async initdata() {
      this.fullscreen();
      var url = "/mobile/routing";
      var pageNum = 1;
      var pageSize = 100000;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageSize,
          pageNum
        },
        this.token
      );
      console.log("xiao", res);
      if (res.data.message === "success") {
        this.loading.close();
      }
      this.listContent = res.data.data.rows;
      var id = this.$route.query.id;
      sessionStorage.setItem("routingName", id);
      for (let index = 0; index < this.listContent.length; index++) {
        var element = this.listContent[index].routingName;
        if (element === id) {
          this.listContentAdd.push(this.listContent[index]);
          this.list = this.listContentAdd[0].monitoringDataList;
          this.list = this.photoTo(this.list);
          console.log(this.listContentAdd);
        }
      }
    },
    photoTo(e) {
      for (let index = 0; index < e.length; index++) {
        const element = e[index].photo;
        var photo = element.split(",");
        e[index].photo = photo[0];
      }
      return e;
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.initdata();
  }
};
</script>
<style scoped>
.subBack {
  width: 100%;
  padding: 0 20px;
  text-align: center;
}
.divWidth {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #4ed995;
  color: white;
  font-size: 16px;
  border-radius: 10px;
  margin-top: 10px;
  font-weight: 600;
}
.flexdis {
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  height: 50px;
  align-items: center;
  background: white;
  /* border-radius: 10px; */
}
.dateTime {
  width: 100%;
}
/* 标题栏 */
.naviTitle {
  margin-top: 10px;
  font-size: 14px;
  font-weight: 600;
  margin-left: 5px;
  border-left: rgb(53, 114, 254) 5px solid;
  margin-bottom: 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  padding-left: 5px;
}
.contentAdd {
  width: 100%;
  /* border-radius: 10px; */
  /* padding: 10px; */
  border-radius: 10px;
}
#input {
  border-radius: 10px;
  width: 100%;
  outline: none;
  padding: 0 10px;
  border: #ccc 1px solid;
  height: 30px;
  background: rgb(239, 240, 243);
}
.padding20 {
  padding: 0 20px;
  margin: 0 5px;
  border-radius: 10px;
  margin-bottom: 5px;
}
.flexdis1 {
  display: flex;
  flex-direction: row;
  height: 40px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px rgb(235, 233, 233) solid;
}
.pointNews {
  margin-bottom: 20px;
}
.fiexdHeader {
  position: fixed;
  top: 0;
}
.imgscene img {
  width: 100%;
  height: auto;
}
.back {
  padding: 10px;
}
.conteBorder {
  border-radius: 10px;
  background: white;
  padding: 10px;
  /* padding: 10px; */
}
</style>


